import { Container, Typography, makeStyles, FormControl,
    Select, InputLabel, MenuItem, Button, TextField} from '@material-ui/core';
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import {useState} from 'react';

const useStyles = makeStyles({
selectValikko: { 
    minWidth: 150,
    marginTop:0,
    marginBottom: 10,
},

tekstikentta:{
    marginTop:5, 
    marginBottom: 5,
    width: 250, 
    padding:0
},
});

const varit = createMuiTheme({
palette: {
  primary: {
    main: "#C91B13"
  },
  secondary: {
    main: "#202833"
  },  
}
});

function SaapasVerbit(props) {
const tyylit = useStyles();

var saapasVerbitApu= [];
props.verbit.map( (epis) => 
{return (epis.alaluokka=="saapasverbi")
?(saapasVerbitApu.push(epis.infinitiivi))
:(null ) }) 

const [verbi, setVerbi] = useState(saapasVerbitApu[0]);
const [vastausY1, setVastausY1]=useState("");
const [vastausY2, setVastausY2]=useState("");
const [vastausY3, setVastausY3]=useState("");
const [vastausM1, setVastausM1]=useState("");
const [vastausM2, setVastausM2]=useState("");
const [vastausM3, setVastausM3]=useState("");
const [virheIlmoitus, setVirheilmoitus] = useState({});
const [oikeinIlmoitus, setOikeinilmoitus] = useState({});
const [kaikkiOikein, setKaikkiOikein] = useState("");

var luku=0;
var muodot = {};
for (var i = 0; i < props.verbit.length; i++){
    if(props.verbit[i].infinitiivi == verbi){
        luku=i;
          
        muodot={  
            infinitiivi: props.verbit[luku].infinitiivi,
            suomeksi: props.verbit[luku].suomeksi,
            y1: props.verbit[luku].y1,
            y2: props.verbit[luku].y2,
            y3: props.verbit[luku].y3,
            m1: props.verbit[luku].m1,
            m2: props.verbit[luku].m2,
            m3: props.verbit[luku].m3,
        }
    }
}          

function tyhjennaKentat(){
    setVastausY1("");   
    setVastausY2("");   
    setVastausY3("");   
    setVastausM1("");
    setVastausM2("");
    setVastausM3("");
    setKaikkiOikein();
    setOikeinilmoitus({});
    setVirheilmoitus({});
}

function tarkistaMuodot(e){
    e.preventDefault();
    var virheet= {};
    var oikein = {};

    if(vastausY1.toLowerCase() != muodot.y1){
        virheet = { ...virheet, y1: "Tarkista taivutus." };
    }else{
        oikein = { ...oikein, y1: " Oikein!" };
    }

    if(vastausY2.toLowerCase() != muodot.y2){
        virheet = { ...virheet, y2: "Tarkista taivutus." };
    }else{
        oikein = { ...oikein, y2: " Oikein!" };
    }

    if(vastausY3.toLowerCase() != muodot.y3){
        virheet = { ...virheet, y3: "Tarkista taivutus." };
    }else{
        oikein = { ...oikein, y3: " Oikein!" };
    }

    if(vastausM1.toLowerCase() != muodot.m1){
        virheet = { ...virheet, m1: "Tarkista taivutus." };
    }else{
        oikein = { ...oikein, m1: " Oikein!" };
    }

    if(vastausM2.toLowerCase() != muodot.m2){
        virheet = { ...virheet, m2: "Tarkista taivutus." };
    }else{
        oikein = { ...oikein, m2: " Oikein!" };
    }

    if(vastausM3.toLowerCase() != muodot.m3){
        virheet = { ...virheet, m3: "Tarkista taivutus." };
    }else{
        oikein = { ...oikein, m3: " Oikein!" };
    }

    setOikeinilmoitus({ ...oikein });  

    if (Object.entries(virheet).length > 0) {
        setVirheilmoitus({ ...virheet });  
    }
    else {
        setKaikkiOikein(`Hienoa! "${verbi}"-verbi meni oikein. Valitse seuraava verbi.`)
        
        let verbiApu = props.verbit.filter((verb) => {
            return (verb.infinitiivi !== verbi);    
        });

        let muokattuVerbi = {
            luokka: props.verbit[luku].luokka,
            alaluokka: props.verbit[luku].alaluokka,
            harjoitusnumero: props.verbit[luku].harjoitusnumero,
            infinitiivi: props.verbit[luku].infinitiivi,
            suomeksi: props.verbit[luku].suomeksi,
            aikamuoto: props.verbit[luku].aikamuoto,
            osataan: true,
            y1: props.verbit[luku].y1,
            y2: props.verbit[luku].y2,
            y3: props.verbit[luku].y3,
            m1: props.verbit[luku].m1,
            m2: props.verbit[luku].m2,
            m3: props.verbit[luku].m3};

        props.setVerbit([...verbiApu, muokattuVerbi]); 
        setVirheilmoitus({});
    }
}

return (
     <MuiThemeProvider theme={varit}>
        <Container>
            <Typography variant ="h6">
                Saapasverbien taivutus
            </Typography>
            <Typography>
                Kiinnitä huomiota myös erikoismerkkeihin. Välilyönnin käyttö on estetty. 
            </Typography>
            <br/>
            <FormControl 
                    variant="outlined" 
                    className={tyylit.selectValikko}>
                <InputLabel>
                    Verbi
                </InputLabel>
                <Select 
                    id="select"
                    value={verbi}
                    label="Verbi"
                    onChange = {(e)=>{setVerbi(e.target.value), tyhjennaKentat() } }>
                
                    {props.verbit.map( (epis, idx) => 
                    {return (epis.alaluokka=="saapasverbi")
                        ?(  <MenuItem 
                                key={idx} 
                                value={epis.infinitiivi} 
                                style={(epis.osataan)?{color:"green"}:{color:"black"}}>
                                {epis.infinitiivi}
                            </MenuItem> )
                        :(null ) }) }

                </Select>
            </FormControl>

            {(kaikkiOikein)
                ?<Typography 
                    variant ="h6" 
                    style={{color:"green"}}>
                    {kaikkiOikein}
                 </Typography>
                :<Typography 
                    variant ="h6">
                    {muodot.infinitiivi} - {muodot.suomeksi} 
                 </Typography>
            }

            <form onSubmit={tarkistaMuodot}>
                <TextField 
                    size="small" 
                    className={tyylit.tekstikentta}
                    variant="outlined"
                    label="Yo"
                    placeholder=""  
                    value={vastausY1}   
                    error={Boolean(virheIlmoitus.y1)}
                    helperText={virheIlmoitus.y1}  
                    onChange = {(e)=>{setVastausY1(e.target.value.replace(/ /g, ''))}}>
                </TextField><a style={{fontSize:14,  color:"green"}}>{oikeinIlmoitus.y1}</a>
                <br/>
                <TextField 
                    float ="left"
                    size="small" 
                    className={tyylit.tekstikentta}
                    variant="outlined"
                    label="Tú"
                    placeholder=""
                    value={vastausY2}  
                    error={Boolean(virheIlmoitus.y2)}
                    helperText={virheIlmoitus.y2}    
                    onChange = {(e)=>{setVastausY2(e.target.value.replace(/ /g, ''))}}>
                </TextField><a style={{fontSize:14,  color:"green"}}>{oikeinIlmoitus.y2}</a>
                 <br/>
                <TextField 
                    size="small" 
                    className={tyylit.tekstikentta}
                    variant="outlined"
                    label="Él"
                    placeholder=""  
                    value={vastausY3}   
                    error={Boolean(virheIlmoitus.y3)}
                    helperText={virheIlmoitus.y3}  
                    onChange = {(e)=>{setVastausY3(e.target.value.replace(/ /g, ''))}}>
                </TextField><a style={{fontSize:14,  color:"green"}}>{oikeinIlmoitus.y3}</a>
                <br/>
                <TextField 
                    size="small" 
                    className={tyylit.tekstikentta}
                    variant="outlined"
                    label="Nosotros"
                    placeholder=""  
                    value={vastausM1}   
                    error={Boolean(virheIlmoitus.m1)}
                    helperText={virheIlmoitus.m1}  
                    onChange = {(e)=>{setVastausM1(e.target.value.replace(/ /g, ''))}}>
                </TextField><a style={{fontSize:14,  color:"green"}}>{oikeinIlmoitus.m1}</a>
                <br/>
                <TextField 
                    float ="left"
                    size="small" 
                    className={tyylit.tekstikentta}
                    variant="outlined"
                    label="Vosotros"
                    placeholder=""
                    value={vastausM2}  
                    error={Boolean(virheIlmoitus.m2)}
                    helperText={virheIlmoitus.m2}    
                    onChange = {(e)=>{setVastausM2(e.target.value.replace(/ /g, ''))}}>
                </TextField><a style={{fontSize:14,  color:"green"}}>{oikeinIlmoitus.m2}</a>
                <br/>
                <TextField 
                    size="small" 
                    className={tyylit.tekstikentta}
                    variant="outlined"
                    label="Ellos"
                    placeholder=""  
                    value={vastausM3}   
                    error={Boolean(virheIlmoitus.m3)}
                    helperText={virheIlmoitus.m3}  
                    onChange = {(e)=>{setVastausM3(e.target.value.replace(/ /g, ''))}}>
                </TextField><a style={{fontSize:14,  color:"green"}}>{oikeinIlmoitus.m3}</a>
                <br/>

                <Button 
                    type="submit"
                    variant="contained"
                    color="primary"
                    style={{marginBottom : 10}}>
                    Tarkista
                </Button>
            </form>
        </Container>
    </MuiThemeProvider>
)
}

export default SaapasVerbit;