import { Container, Typography } from '@material-ui/core';

function Aloitus() {

    return (
        <Container >
           <Typography variant ="h5" style={{marginLeft:0, marginTop:20}}>¡Bienvenidos!</Typography> 
            <Typography style={{marginLeft:0, marginTop:10, fontSize:18}}>
                    Tällä sovelluksella voit harjoitella Espanja 1 ja Espanja 2 kurssin verbien preesenstaivutuksia. 
            </Typography>
            
            <ul style={{maxWidth:700, fontSize:18}}>
                <li>
                    Voit valita valikosta joko säännölliset verbit, epäsäännölliset verbit tai niin kutsutut saapasverbit.
                </li>
                <li>
                    Kun taivutus on mennyt kerran oikein, niin verbin väri valikossa muuttuu vihreäksi.
                </li>
                <li>
                    Voit lisätä harjoituksiin myös omia verbejä. Lisäyksen yhteydessä ne tallentuvat selaimen välimuistiin. 
                </li>
                <li>
                    Voit poistaa verbejä. Huomio kuitenkin, että kaikkiin luokkiin jää vähintään yksi verbi.
                </li>
                <li>
                    Voit poistaa kaikki omat lisäyksesi kerralla palauttamalla sovelluksen aloitustilaan. 
                </li>
            </ul>
            
        </Container>
    )
}

export default Aloitus