import {useState} from 'react';
import {AppBar ,IconButton,Toolbar, CssBaseline, 
        Drawer, List, ListItem, ListItemText, ListItemIcon, Typography} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import HomeIcon from '@material-ui/icons/Home';
import DeleteIcon from '@material-ui/icons/Delete';
import {Link} from 'react-router-dom';
import LabelImportantIcon from '@material-ui/icons/LabelImportant';
import CreateIcon from '@material-ui/icons/Create';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
    paper: {
      background: "#FFC600",
      color: "black"
    },
    valikko:{
        color:"#FFC600",
        backgroundColor:"#C91B13",
    }
  });

function Valikko() {

    const styles = useStyles();
    const [naytaValikko, setNaytaValikko] = useState(false);

    return (
        <CssBaseline>
            <AppBar position="static" className={styles.valikko}>
                <Toolbar>
                    <IconButton 
                        edge="start" 
                        color="inherit"
                        onClick={ () => { setNaytaValikko(true) } }
                    >
                        <MenuIcon />
                        <Typography style={{marginLeft:10}}></Typography><Typography style={{marginLeft:20, fontSize:26}}>Los verbos en español</Typography>
                    </IconButton>
                    <Drawer 
                        open={naytaValikko}
                        onClose={ () => { setNaytaValikko(false) } }
                        classes={{ paper: styles.paper }}
                    >
                        
                        <List style={{
                                width : 220,
                                marginTop : 50 
                            }}
                            onClick={ () => { setNaytaValikko(false) } }
                        >
                            <ListItem button component={Link} to="/">
                                <ListItemIcon>
                                    <HomeIcon />
                                </ListItemIcon>
                                <ListItemText primary="Aloitus"/>
                            </ListItem>

                            <ListItem button component={Link} to="/VerbitEpasaannolliset">
                                <ListItemIcon>
                                    <LabelImportantIcon />
                                </ListItemIcon>
                                <ListItemText primary="Epäsäännölliset verbit"/>
                            </ListItem>

                            <ListItem button component={Link} to="/VerbitSaannolliset">
                                <ListItemIcon>
                                    <LabelImportantIcon />
                                </ListItemIcon>
                                <ListItemText primary="Säännölliset verbit"/>
                            </ListItem>

                            <ListItem button component={Link} to="/SaapasVerbit">
                                <ListItemIcon>
                                    <LabelImportantIcon />
                                </ListItemIcon>
                                <ListItemText primary="Saapasverbit"/>
                            </ListItem>

                            <ListItem button component={Link} to="/VerbitLisays">
                                <ListItemIcon>
                                    <CreateIcon />
                                </ListItemIcon>
                                <ListItemText primary="Verbin lisäys"/>
                            </ListItem>

                            <ListItem button component={Link} to="/VerbinPoisto">
                                <ListItemIcon>
                                    <DeleteIcon />
                                </ListItemIcon>
                                <ListItemText primary="Poisto ja tyhjennys"/>
                            </ListItem>
                        </List>
                    </Drawer>
                </Toolbar>
            </AppBar>
        </CssBaseline>
    )
}

export default Valikko;