import { Container, Typography, makeStyles, FormControl,Select, InputLabel, MenuItem, Button, TextField } from '@material-ui/core';
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import {v4 as uuid} from 'uuid';
import {useState} from 'react';

const useStyles = makeStyles({

selectValikko: {
    minWidth: 150,
    marginTop:10,
    marginBottom: 10,
  },

  tekstikentta:{
    marginTop:5, marginBottom: 5,
    width: 250, padding:0
  },
});

const varit = createMuiTheme({
  palette: {
    primary: {
      main: "#C91B13"
    },
    secondary: {
      main: "#202833"
    },
  }
});

function VerbitLisays(props) {
  const tyylit = useStyles();

  const [virheIlmoitus, setVirheilmoitus] = useState({});
  const [lisaysViesti, setLisaysViesti] = useState("");
  const [lisaysInf, setLisaysInf]=useState("");
  const [lisaysSuo, setLisaysSuo]=useState("");
  const [lisaysY1, setLisaysY1]=useState("");
  const [lisaysY2, setLisaysY2]=useState("");
  const [lisaysY3, setLisaysY3]=useState("");
  const [lisaysM1, setLisaysM1]=useState("");
  const [lisaysM2, setLisaysM2]=useState("");
  const [lisaysM3, setLisaysM3]=useState("");

  var alaluokat= [];
  for (var i = 0; i < props.verbit.length; i++){
   var yrite= props.verbit[i].alaluokka;
   if(yrite=="saannollinen"){yrite="säännöllinen"}
   if(yrite=="epasaannollinen"){yrite="epäsäännöllinen"}
    if(alaluokat.includes(yrite)){
  
    }else{
        alaluokat.push(yrite);
    }
  }

 var MuunnaAakkoset={};
  MuunnaAakkoset.map={
      "Ä":"A",
      "Å":"A",
      "Ö":"O",
      "ä":"a",
      "ö":"o",
      "å":"a"
      };
  String.prototype.muunnaAakkoset=function(){
    return this.replace(/[^A-Za-z0-9\[\] ]/g,
      function(a){return MuunnaAakkoset.map[a]||a}
  )};
 
 const [lisaysAlaluokka, setLisaysAlaluokka]=useState(alaluokat[0]);

 function tyhjennaKentat(){
  setLisaysInf("");
  setLisaysSuo("");      
  setLisaysY1("");   
  setLisaysY2("");   
  setLisaysY3("");
  setLisaysM1("");   
  setLisaysM2("");   
  setLisaysM3("");   
  setVirheilmoitus({});
}

function lisaaVerbi(e){
 
  e.preventDefault();
  var virheet= {};

  if(lisaysInf.length<2){
    virheet = { ...virheet, inf: "Anna verbin perusmuoto." }
  }else if(lisaysInf.match(/\d/)){
    virheet = { ...virheet, inf: "Sana ei voi sisältää numeroita." }}

    if(lisaysSuo.length<2){
      virheet = { ...virheet, suo: "Anna verbin suomennos." }
    }else if(lisaysSuo.match(/\d/)){
      virheet = { ...virheet, suo: "Sana ei voi sisältää numeroita." }}

  if(lisaysY1.length<2){
      virheet = { ...virheet, y1: "Anna yksikön 1. muoto." }
    }else if(lisaysY1.match(/\d/)){
      virheet = { ...virheet, y1: "Sana ei voi sisältää numeroita." }}

  if(lisaysY2.length<2){
      virheet = { ...virheet, y2: "Anna yksikön 2. muoto." }
  }else if(lisaysY2.match(/\d/)){
      virheet = { ...virheet, y2: "Sana ei voi sisältää numeroita." }}    

  if(lisaysY3.length<2){
      virheet = { ...virheet, y3: "Anna yksikön 3. muoto." }
  }else if(lisaysY3.match(/\d/)){
      virheet = { ...virheet, y3: "Sana ei voi sisältää numeroita." }}    

  if(lisaysM1.length<2){
    virheet = { ...virheet, m1: "Anna monikon 1. muoto." }
  }else if(lisaysM1.match(/\d/)){
    virheet = { ...virheet, m1: "Sana ei voi sisältää numeroita." }}

    if(lisaysM2.length<2){
      virheet = { ...virheet, m2: "Anna monikon 2. muoto." }
    }else if(lisaysM2.match(/\d/)){
      virheet = { ...virheet, m2: "Sana ei voi sisältää numeroita." }}

    if(lisaysM3.length<2){
      virheet = { ...virheet, m3: "Anna monikon 3. muoto." }
    }else if(lisaysM3.match(/\d/)){
      virheet = { ...virheet, m3: "Sana ei voi sisältää numeroita." }}

    if (Object.entries(virheet).length > 0) {
      setVirheilmoitus({ ...virheet });  
  }
  else {
      let uusiVerbi = {
          id: uuid(),
          luokka: "verbi",
          alaluokka: lisaysAlaluokka.muunnaAakkoset().toLowerCase(),
          harjoitusnumero: 2,
          infinitiivi: lisaysInf.muunnaAakkoset().toLowerCase(),
          suomeksi: lisaysSuo.toLowerCase(),
          aikamuoto: "preesens",
          osataan: false,
          y1: lisaysY1.muunnaAakkoset().toLowerCase(),
          y2: lisaysY2.muunnaAakkoset().toLowerCase(),
          y3: lisaysY3.muunnaAakkoset().toLowerCase(),
          m1: lisaysM1.muunnaAakkoset().toLowerCase(),
          m2: lisaysM2.muunnaAakkoset().toLowerCase(),
          m3: lisaysM3.muunnaAakkoset().toLowerCase()
        };

      let verbiApu = props.verbit.filter((verb) => {
          return (verb.infinitiivi !== lisaysInf);
      });
      
      props.setVerbit([...verbiApu, uusiVerbi]);
      setLisaysViesti(`  Verbi "${lisaysInf}" on nyt lisätty.`)
      tyhjennaKentat();
    
      }
  }

  return (
    <MuiThemeProvider theme={varit}>
      <Container>
          <Typography variant ="h6">
              Verbin lisäys
          </Typography>
        <form onSubmit={lisaaVerbi} >
          <TextField 
            style={{marginRight:10}}
            size="small" 
            className={tyylit.tekstikentta}
            variant="outlined"
            label="Verbi (infinitiivi)"
            placeholder=""  
            value={lisaysInf}   
            error={Boolean(virheIlmoitus.inf)}
            helperText={virheIlmoitus.inf}  
            onChange = {(e)=>{setLisaysInf(e.target.value.replace(/ /g, '')), setLisaysViesti("")}}
          ></TextField>
          <TextField 
            size="small" 
            className={tyylit.tekstikentta}
            variant="outlined"
            label="Suomeksi"
            placeholder=""  
            value={lisaysSuo}   
            error={Boolean(virheIlmoitus.suo)}
            helperText={virheIlmoitus.suo}  
            onChange = {(e)=>{setLisaysSuo(e.target.value.replace(/ /g, '')), setLisaysViesti("")}}
          ></TextField>
          <br/>
          <FormControl variant="outlined" size="small"
            className={tyylit.selectValikko}>
            <InputLabel>
            Verbiluokka
            </InputLabel>
            <Select id="select"
              value={lisaysAlaluokka}
              label="Verbiluokka"
              onChange = {(e)=>{setLisaysAlaluokka(e.target.value)} }>
              
              {alaluokat.map( (luokka, idx) => {
                return ( 
                <MenuItem key={idx} value={alaluokat[idx]}>
                  {luokka}
                </MenuItem> 
                ) })
              }
            </Select>
          </FormControl>
             <br/>
          <TextField 
              size="small" 
              className={tyylit.tekstikentta}
              variant="outlined"
              label="Yo"
              placeholder=""  
              value={lisaysY1}   
              error={Boolean(virheIlmoitus.y1)}
              helperText={virheIlmoitus.y1}  
              onChange = {(e)=>{setLisaysY1(e.target.value.replace(/ /g, '')), setLisaysViesti("")}}
            ></TextField>
            <br/>
          <TextField 
              size="small" 
              className={tyylit.tekstikentta}
              variant="outlined"
              label="Tú"
              placeholder=""  
              value={lisaysY2}   
              error={Boolean(virheIlmoitus.y2)}
              helperText={virheIlmoitus.y2}  
              onChange = {(e)=>{setLisaysY2(e.target.value.replace(/ /g, '')), setLisaysViesti("")}}
            ></TextField>
            <br/>
          <TextField 
              size="small" 
              className={tyylit.tekstikentta}
              variant="outlined"
              label="Él"
              placeholder=""  
              value={lisaysY3}   
              error={Boolean(virheIlmoitus.y3)}
              helperText={virheIlmoitus.y3}  
              onChange = {(e)=>{setLisaysY3(e.target.value.replace(/ /g, '')), setLisaysViesti("")}}
            ></TextField>
            <br/>
          <TextField 
              size="small" 
              className={tyylit.tekstikentta}
              variant="outlined"
              label="Nosotros"
              placeholder=""  
              value={lisaysM1}   
              error={Boolean(virheIlmoitus.m1)}
              helperText={virheIlmoitus.m1}  
              onChange = {(e)=>{setLisaysM1(e.target.value.replace(/ /g, '')), setLisaysViesti("")}}
            ></TextField>
            <br/>
          <TextField 
              size="small" 
              className={tyylit.tekstikentta}
              variant="outlined"
              label="Vosotros"
              placeholder=""  
              value={lisaysM2}   
              error={Boolean(virheIlmoitus.m2)}
              helperText={virheIlmoitus.m2}  
              onChange = {(e)=>{setLisaysM2(e.target.value.replace(/ /g, '')), setLisaysViesti("")}}
            ></TextField>
            <br/>
          <TextField 
              size="small" 
              className={tyylit.tekstikentta}
              variant="outlined"
              label="Ellos"
              placeholder=""  
              value={lisaysM3}   
              error={Boolean(virheIlmoitus.m3)}
              helperText={virheIlmoitus.m3}  
              onChange = {(e)=>{setLisaysM3(e.target.value.replace(/ /g, '')), setLisaysViesti("")}}
            ></TextField>
             <br/>
          <Button 
              color="primary"
              type="submit"
              variant="contained"

              style={{marginBottom : 10}}>
              Lisää
          </Button> <a style={{fontSize:16,  color:"green"}}>{lisaysViesti}</a>
            <br/>
        </form> 
      </Container>
    </MuiThemeProvider>
  )
}
export default VerbitLisays;
