    const handleClickOpen = () => {
      setOpen(true);
    };
  
    const handleClose = () => {
      setOpen(false);
    };

    function poistaVerbi(e){ 
      e.preventDefault();
      var virheet= {};
      var alaluokkaApu = "";
      var lukumaara=0;
    
      for (var i = 0; i < props.verbit.length; i++){
         if(props.verbit[i].infinitiivi==poistettavaVerbi.toLowerCase()){
           alaluokkaApu = props.verbit[i].alaluokka;
         }
      }
        for (var i = 0; i < props.verbit.length; i++){
          if(props.verbit[i].alaluokka==alaluokkaApu){
            lukumaara = lukumaara+1;
          }
        }

      var loytyyko= false;
      for (var i = 0; i < props.verbit.length; i++){
        if(props.verbit[i].infinitiivi==poistettavaVerbi.toLowerCase()){
          loytyyko = true;
        }else{

        }
     }

      if(poistettavaVerbi.length<2){
          virheet = { ...virheet, nimi: "Verbin pituus on vähintään 2 kirjainta." };
      }
      else if(!loytyyko){
        virheet = { ...virheet, nimi: "Verbiä ei ole listassa." };
      }
      else if(lukumaara<2){
        virheet = { ...virheet, nimi: "Jokaiseen verbiluokkaan pitää jättää vähintään yksi verbi." };
      }
      
          if (Object.entries(virheet).length > 0) {
              setVirheilmoitus({ ...virheet });  
          }
          else {
  
              let verbiApu = props.verbit.filter((verb) => {
                  return (verb.infinitiivi !== poistettavaVerbi.toLowerCase());
              });
              setPoistoViesti(`Verbi "${poistettavaVerbi}" on nyt poistettu.`); 
              props.setVerbit([...verbiApu]);
  
              setVirheilmoitus({});
              setPoistettavaVerbi("");
      }
  }
  
  function nollaaOsatut(){
    var verbitApu = [];
    for (var i = 0; i < props.verbit.length; i++){
      let verbiApu = {
        id: props.verbit[i].id,
        luokka: props.verbit[i].luokka,
        alaluokka: props.verbit[i].alaluokka,
        harjoitusnumero: props.verbit[i].harjoitusnumero,
        infinitiivi: props.verbit[i].infinitiivi,
        suomeksi: props.verbit[i].suomeksi,
        aikamuoto: props.verbit[i].aikamuoto,
        osataan: false,
        y1: props.verbit[i].y1,
        y2: props.verbit[i].y2,
        y3: props.verbit[i].y3,
        m1: props.verbit[i].m1,
        m2: props.verbit[i].m2,
        m3: props.verbit[i].m3
      };
      verbitApu.push(verbiApu);
    }
    props.setVerbit([...verbitApu]);
    setNollausViesti("Osaamiset on nyt nollattu.")
  }

